<template>
  <div class="area-main">
    
    <div class="main">
      <div
        class="avatars-list"
      >
        <div
          v-for="avatarId in this.avatarsList"
          :key="avatarId"
          class="tile-blue tile-round"
          :class="[`svg-avatar-${avatarId}`, {disabled: this.usedAvatarIds.includes(avatarId)}, (this.currentAvatarId === avatarId ? 'bg-white' : 'tile-blue')]"
          @click="() => {if (!this.usedAvatarIds.includes(avatarId)) this.currentAvatarId = avatarId}"
        />
      </div>
    </div>
    <ControlsPanelBase>
      <template #controls-first>
        <div 
          @click="this.$router.push({ name: 'home' })"
          class="tile-round bg-blue svg-back">
        </div>
      </template>
      <template #controls-last>
        <div 
          @click="this.createProfileHandler()"
          class="tile-blue tile-round svg-check"
          :class="{disabled: this.currentAvatarId === null}"
        >
        </div>
      </template>
    </ControlsPanelBase>
  </div>
</template>

<script>
import DomMixin from '@/mixins/domMixin';
import ControlsPanelBase from '@/components/ControlsPanelBase';
import ProfileMixin from '@/mixins/profileMixin';
export default {
    mixins: [
      DomMixin,
      ProfileMixin,
    ],
    created() {
        this.domSetDocumentBackgroundUrl(require('/public/img/bg-hands-1080p.png'));
    },
    mounted() {
      this.usedAvatarIds = this.loadProfiles().map(v => v.avatarId)
    },
    components: {
      ControlsPanelBase
    },
    data() {
      return {
        avatarsList: [1,7,2,8,9,3,10,4,5,11,6,12],
        currentAvatarId: null,
        usedAvatarIds: []
      }
    },
    methods: {
      createProfileHandler() {
        if (this.currentAvatarId) {
          this.setCurrentProfile(this.createProfile(this.currentAvatarId).id)
          this.$router.push({ name: 'home' })
        }
      }
    },
}
</script>

<style scoped lang="scss">
.area-main {
  display: flex;
}
.main {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.avatars-list {
  display: flex;
  flex-wrap: wrap;
  width: 48vw;
  gap: 3vw; 
  justify-content: center;

  .disabled {
    filter: grayscale(80%);
  }
}
</style>